body {
    margin: 0;
    padding: 0;
    /*font-family: sans-serif;*/
    text-align: center;
    background: whitesmoke;
    color: #4f4f4f;
    font-family: "Consolas", monospace;
    font-size: 16px;
    line-height: 2.5;
}

#backgroundWrapper {
    display: -ms-inline-flexbox;
    display: inline-flex;
    margin: 20px auto;
    padding: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -ms-flex-direction: column;
        flex-direction: column;
}

#backgroundWrapper > * {
    -ms-flex-item-align: center;
        align-self: center;
}


.bigSpacer {
    margin: 40px 0;
}

#content {
    display: -ms-flexbox;
    display: flex;
    text-align: center;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 5px;
    max-width: 50vw;
}

@media (max-width: 550px) {
    #content {
        max-width: 85vw;
    }

    #backgroundWrapper {
        padding: 0 !important;
    }
}

hr {
    width: 382px;
}

.startTour {
    display: inline;
    margin: 10px;
    background-color: #5d9bc3;
    color: white;
    border: none;
    padding: 10px;
    border-radius: 2px;
    font-size: inherit;
    font-family: inherit;
}

/* Form */
#formTitle {
    text-decoration: underline;
}

.inputWrapper {
    padding: 10px;
    background: #e7e7e7;
    margin-bottom: 10px;
    line-height: 2;
}

input[type=radio] {
    margin: 0 10px;
}

input[type=text] {
    border: 2px solid lightgrey;
}

form + button {
    width: 100%;
    margin: 0 !important;
}

.success {
    border: 1px solid forestgreen;
    color: forestgreen;
    padding: 10px;
}

.errorBox {
    border: 2px solid #ff553c;
    border-radius: 1px;
    color: darkred;
    padding: 10px;
    font-size: 14px;
}

#prologin {
    -webkit-transition: -webkit-transform 2s ease-in-out;
    transition: -webkit-transform 2s ease-in-out;
    -o-transition: transform 2s ease-in-out;
    transition: transform 2s ease-in-out;
    transition: transform 2s ease-in-out, -webkit-transform 2s ease-in-out
}

#prologin:hover {
    -webkit-transform: rotate(360deg);
        -ms-transform: rotate(360deg);
            transform: rotate(360deg)
}



